import React, { useState, useEffect, useCallback } from 'react';
import { fetchReviewsAPI, createReviewAPI } from '../axiosInstance';

const ReviewSection = ({ courseUid }) => {
    const [reviews, setReviews] = useState([]);
    const [content, setContent] = useState('');
    const [rating, setRating] = useState(5);
    const [writer, setWriter] = useState('');
    const [isFormVisible, setIsFormVisible] = useState(false);
    const [currentPage, setCurrentPage] = useState(1);
    const reviewsPerPage = 5;

    const fetchReviews = useCallback(async () => {
        try {
            const response = await fetchReviewsAPI(courseUid);
            setReviews(response.data);
        } catch (error) {
            console.error("  :", error);
        }
    }, [courseUid]);

    useEffect(() => {
        if (courseUid) fetchReviews();
    }, [courseUid, fetchReviews]);

    const handleSubmit = async () => {
        if (!content.trim() || !writer.trim()) {
            return alert("   .");
        }

        try {
            await createReviewAPI({
                courseUid: Number(courseUid),
                writer: writer,
                content: content,
                rating: Number(rating)
            });
            
            setContent('');
            setWriter('');
            setRating(5);
            setIsFormVisible(false);
            fetchReviews();
            setCurrentPage(1);
            alert(" !");
        } catch (error) {
            console.error(" :", error);
            alert("  .");
        }
    };

    const indexOfLastReview = currentPage * reviewsPerPage;
    const indexOfFirstReview = indexOfLastReview - reviewsPerPage;
    const currentReviews = reviews.slice(indexOfFirstReview, indexOfLastReview);
    const totalPages = Math.ceil(reviews.length / reviewsPerPage);

    return (
        <div className="review-section-container">
            <div className="review-list-header" style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <h2 style={{ fontSize: '20px' }}> <span className="review-count">{reviews.length}</span></h2>
                {!isFormVisible && (
                    <button className="show-form-btn" onClick={() => setIsFormVisible(true)}>+  </button>
                )}
            </div>

            {isFormVisible && (
                <div className="review-form-box">
                    <div className="form-top">
                        <input type="text" placeholder="" className="review-writer-input" value={writer} onChange={(e) => setWriter(e.target.value)} />
                        <div className="rating-selection">
                            {[5, 4, 3, 2, 1].map((num) => (
                                <label key={num} className="rating-label">
                                    <input type="radio" name="rating" value={num} checked={rating === num} onChange={() => setRating(num)} />
                                    <div className="star-number-box">
                                        <span className="star-icon"></span>
                                        <span className="number-overlay">{num}</span>
                                    </div>
                                </label>
                            ))}
                        </div>
                    </div>
                    <textarea placeholder="    ." className="review-content-textarea" value={content} onChange={(e) => setContent(e.target.value)}></textarea>
                    <div className="form-bottom" style={{ display: 'flex', gap: '10px', justifyContent: 'flex-end' }}>
                        <button type="button" onClick={() => setIsFormVisible(false)} style={{ background: '#eee', border: 'none', padding: '8px 15px', borderRadius: '6px', cursor: 'pointer' }}></button>
                        <button className="review-submit-btn" onClick={handleSubmit}></button>
                    </div>
                </div>
            )}

            <div className="review-list">
                {currentReviews.length > 0 ? (
                    currentReviews.map(r => (
                        <div key={r.uid} className="review-card">
                            <div className="card-header">
                                <span className="card-writer">{r.writer}</span>
                                <div className="card-stars-wrapper">
                                    <span style={{ fontSize: '12px' }}></span>
                                    <span className="card-rating-num">{r.rating}</span>
                                </div>
                                <div className="card-body-flexible">{r.content}</div>
                                <span className="card-date">
                                    {r.createdAt ? new Date(r.createdAt).toLocaleDateString() : ""}
                                </span>
                            </div>
                        </div>
                    ))
                ) : (
                    <div className="empty-reviews">   !</div>
                )}
            </div>

            {/*   */}
            {totalPages > 1 && (
                <div className="pagination">
                    {Array.from({ length: totalPages }, (_, i) => i + 1).map(pageNum => (
                        <button 
                            key={pageNum} 
                            className={`page-btn ${currentPage === pageNum ? 'active' : ''}`}
                            onClick={() => setCurrentPage(pageNum)}
                        >
                            {pageNum}
                        </button>
                    ))}
                </div>
            )}
        </div>
    );
};

export default ReviewSection;