import { useCallback, useEffect, useRef, useState } from 'react';
import { useAuth } from '../context/AuthContext';
import "./Tapmodalbase.css";
import { useNavigate } from 'react-router-dom';
import { authInstance, courseInstance, signIn, signUp } from '../axiosInstance';

export function Tapmodalbase({onClose}){
    const [onTap, setOnTap] =useState('left');

    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [phone, setPhone] = useState('')
    const [agreeterm, setAgreeterm] = useState(false)
    const [keepLoggedIn, setKeepLoggedIn] = useState(false);
    const [receiveMarketing, setreceiveMarketing] = useState(false)
    const DEFAULT_TUTOR_STATUS = 'n';

    const {signInSuccess} = useAuth();
    const navigate = useNavigate();
    
    const handleNameChange = (e) => setName(e.target.value);
    const handleEmailChange = (e) => setEmail(e.target.value);
    const handlePasswordChange = (e) => setPassword(e.target.value);
    const handlePhoneChange = (event) => {
        let value = event.target.value;
        
        const cleanedValue = value.replace(/[^0-9]/g, '');
        let formattedValue = '';
        
        if (cleanedValue.length < 4){
            formattedValue = cleanedValue;
        }else if (cleanedValue.length < 8){
            formattedValue = cleanedValue.substring(0, 3) + '-' + cleanedValue.substring(3);            
        }else if (cleanedValue.length <= 11){
            formattedValue = cleanedValue.substring(0, 3) + '-' + cleanedValue.substring(3, 7) + '-' + cleanedValue.substring(7);
        }else{
        formattedValue = cleanedValue.substring(0, 11);
        formattedValue = formattedValue.substring(0, 3) + '-' +formattedValue.substring(3, 7) + '-' + formattedValue.substring(7);
    }
    setPhone(formattedValue);
};

    const handleTermsChange = (e) => {setAgreeterm(e.target.checked);};
    const handleMarketingChange = (e) => {setreceiveMarketing(e.target.checked);};
    const handleKeepLoggedInChange = (e) => {
        setKeepLoggedIn(e.target.checked);
    };

    const modalRef = useRef(null);
    const initalFocusRef = useRef(null);
    const activeClass = 'active';
    const handleTapClick = (tapname) =>{
        setOnTap(tapname);
        }

    const handleSignInSubmit = async (e) => {
        e.preventDefault();

        const signInData = {
            email: email,
            password: password,
        };

        try{
            const response = await signIn(signInData);
            signInSuccess(response.data);

            navigate('/');
            console.log(" ");
            
            if (onClose){
                onClose();
            }
        } catch (error){
            console.error(" : ", error);
        }
    };

    const handleSignupSubmit = async () => {
        if (!agreeterm){
            console.error("    .");
            alert("   .");
            return;
        }
        const cleanedPhoneNumber = phone.replace(/-/g, '');
        const signUpData = {
            name: name,
            email: email,
            password: password,
            phoneNumber: cleanedPhoneNumber,
            agreeTerms: agreeterm,
            receiveMarketing: receiveMarketing,
            tutor: DEFAULT_TUTOR_STATUS
        };
        try{
            console.log(" :", signUpData);
            const response = await signUp(signUpData);
            console.log(" ", response.data);
            if(onClose){
                onClose();
            }
        }catch (error){
            console.error(" : ", error);
        }
    };

    const handleKeyDown = useCallback((event) => {
        if (event.key === "Escape"){
            if (onClose){
            onClose();
        }
        }else if (event.key === "Tab" && modalRef.current){
            const focusableElements = modalRef.current.querySelectorAll(
                "button, [href], input, select, textarea, [tabindex]:not([tabindex='-1'])"
            );
            const firstElement = focusableElements[0];
            const lastElement = focusableElements[focusableElements.length -1];

            if (event.shiftKey){
                if(document.activeElement === firstElement){
                    lastElement.focus();
                    event.preventDefault();
                }
            }else{
                if (document.activeElement === lastElement){
                    firstElement.focus();
                    event.preventDefault();
                }
            }
        }
    },[onClose]);

    useEffect(()=>{
        if (modalRef.current){
            document.addEventListener('keydown', handleKeyDown);

            if (initalFocusRef.current){
                initalFocusRef.current.focus();
            }
        }
        return() => {
            document.removeEventListener('keydown', handleKeyDown);
        };
    },[handleKeyDown]);

    useEffect(() => {
        const handleClickOutside = (event) =>{
            if (modalRef.current && !modalRef.current.contains(event.target)){
                event.preventDefault();
                event.stopPropagation();

                if (initalFocusRef.current){
                    initalFocusRef.current.focus();
                }
            }
        };
        document.addEventListener('mousedown', handleClickOutside, true);
        document.addEventListener('click', handleClickOutside, true);

        return () => {
            document.removeEventListener('mousedown', handleClickOutside, true);
            document.removeEventListener('click', handleClickOutside, true);
        };
    }, [initalFocusRef]);

    useEffect(() => {
        document.body.classList.add('modal-open');
        return() => {
            document.body.classList.remove('modal-open');
        };
    },[]);

return(
    <div id='tap-wrapper' role='dialog' aria-modal="true" ref={modalRef} tabIndex={-1} onClick={(e) => e.stopPropagation()}>
        <div className='Always-area'><img alt=' ' className="title-image" src="/image/ELC.svg" />
            <span className='catchphrase'>  </span>
                <div className='buttonArea'>
                    <button className={`tap-button ${onTap === "left" ? activeClass:''}`}
                    onClick={()=>handleTapClick("left")} ref={onTap === 'left' ? initalFocusRef : null}></button>
                    <button className={`tap-button ${onTap === "right" ? activeClass:''}`}
                    onClick={()=>handleTapClick("right")} ref={onTap === 'right' ? initalFocusRef : null}></button>
                </div>
            </div>
        <div>
            {onTap === "left"&&(
                <div className='signinArea'>                    
                    <span className='email'></span>
                    <img src='/image/e-mail.svg' alt=' ' className='email-icon' width="24" height="24" />
                    <input type='text' className='sign-email' placeholder=' ' value={email} onChange={handleEmailChange}/>
                    <span className='password'></span>
                    <img src='/image/lock.svg' alt=' ' className='lock-icon' width="24" height="24" />
                    <input type='password' className='sign-password' placeholder=' ' value={password} onChange={handlePasswordChange}/> 
                    <input type='checkbox' className='sign-checkbox' checked={keepLoggedIn} onChange={handleKeepLoggedInChange}/>
                    <span className='text'>  </span> 
                    <span className='search-pass'> </span>
                    <button className='signin-button' onClick={handleSignInSubmit}></button>
                </div>
            )}
            {onTap === "right"&&(
                <div className='signupArea'>
                    <span className='upName'></span>
                    <img src='/image/person.svg' alt=' ' className='person-icon' width="24" height="24" />
                    <input type='text' className='up-name' placeholder=' ' value={name} onChange={handleNameChange}/>
                    <span className='upEmail'></span>
                    <img src='/image/e-mail.svg' alt=' ' className='email-icon' width="24" height="24" />
                    <input type='text' className='up-email' placeholder=' ' value={email} onChange={handleEmailChange}/>
                    <span className='upPass'></span>
                    <img src='/image/lock.svg' alt=' ' className='lock-icon' width="24" height="24" />
                    <input type='password' className='up-pass' placeholder=' ' value={password} onChange={handlePasswordChange}/>
                    <span className='upNumber'></span>
                    <img src='/image/phone.svg' alt=' ' className='phone-icon' width="24" height="24" />
                    <input type='tel' className='up-number' placeholder='010-0000-0000' value={phone} onChange={handlePhoneChange}/>
                    <input type='checkbox' className='first-check' checked={agreeterm} onChange={handleTermsChange} />
                    <span className='text-1'>   .</span>
                    <input type='checkbox' className='second-check' checked={receiveMarketing} onChange={handleMarketingChange} />
                    <span className='text-2'>   .()</span>
                    <button className='signup-button' onClick={handleSignupSubmit} disabled={!agreeterm}></button>
                </div>
            )}
        </div>
    </div>
);
}

export function AdmPage({ onClose }) {
    
    const [courseName, setCourseName] = useState('');
    const [courseline, setCourseline] = useState('');
    const [subcoursename, setsubcoursename] = useState(''); 
    const [imagename, setImagename] = useState('');
    const [videourl, setvideourl] = useState('');
    const [courseprice, setCourseprice] = useState('');

    const handlecourseNameChange = (e) => setCourseName(e.target.value);
    const handlecourselineChange = (e) => setCourseline(e.target.value);
    const handlesubcoursenameChange = (e) => setsubcoursename(e.target.value);
    const handleimagenameChange = (e) => setImagename(e.target.value);
    const handlevideourlChange = (e) => setvideourl(e.target.value);
    const handlecoursepriceChange = (e) => setCourseprice(e.target.value);

    const modalRef = useRef(null);
    const initalFocusRef = useRef(null);

    const handlecourseSubmit = async () => {
        if (!courseName || !courseline || !courseprice) {
            alert(', ,    .');
            return;
        }

        const courseRegData = {
            courseName: courseName,
            courseline: courseline,
            subcoursename: subcoursename,
            imagename: imagename,
            videourl: videourl,
            courseprice: courseprice,
        };
        
        try {
            console.log("   :", courseRegData);
            const response = await courseInstance.post('/course', courseRegData);
            console.log(" ", response.data);
            alert('  .');
            if (onClose) {
                onClose();
            }
        } catch (error) {
            console.error(" : ", error);
            alert(`  : ${error.message}`);
        }
    };

    const handleKeyDown = useCallback((event) => {
        if (event.key === "Escape") {
            if (onClose) {
                onClose();
            }
        } else if (event.key === "Tab" && modalRef.current) {
            const focusableElements = modalRef.current.querySelectorAll(
                "button, [href], input, select, textarea, [tabindex]:not([tabindex='-1'])"
            );
            const firstElement = focusableElements[0];
            const lastElement = focusableElements[focusableElements.length - 1];

            if (event.shiftKey) {
                if (document.activeElement === firstElement) {
                    lastElement.focus();
                    event.preventDefault();
                }
            } else {
                if (document.activeElement === lastElement) {
                    firstElement.focus();
                    event.preventDefault();
                }
            }
        }
    }, [onClose]);

    useEffect(() => {
        if (modalRef.current) {
            document.addEventListener('keydown', handleKeyDown);
            
            if (initalFocusRef.current) {
                initalFocusRef.current.focus();
            }
        }
        return () => {
            document.removeEventListener('keydown', handleKeyDown);
        };
    }, [handleKeyDown]);

    useEffect(() => {
        const handleClickOutside = (event) => {
            if (modalRef.current && !modalRef.current.contains(event.target)) {
                if (initalFocusRef.current) {
                    initalFocusRef.current.focus();
                }
            }
        };
        document.addEventListener('mousedown', handleClickOutside, true); 
        document.addEventListener('click', handleClickOutside, true);

        return () => {
            document.removeEventListener('mousedown', handleClickOutside, true);
            document.removeEventListener('click', handleClickOutside, true);
        };
    }, []);
    
    useEffect(() => {
        document.body.classList.add('modal-open');
        return() => {
            document.body.classList.remove('modal-open');
        };
    },[]);

    return (
        <div id='adm-wrapper' role='dialog' aria-modal="true" ref={modalRef} tabIndex={-1} onClick={(e) => e.stopPropagation()}>
            <div className='adm-Always-area'>
                <img alt=' ' className="title-image" src="/image/ELC.svg" />
                <h2 className='adm-title'> </h2> 
            </div>
            
            <div className='courseregArea'>
                <span className='courseName'></span>
                <input 
                    type='text' 
                    className='course-name' 
                    placeholder=' ' 
                    value={courseName} 
                    onChange={handlecourseNameChange} 
                    ref={initalFocusRef}
                /> 
                
                <label className='courseCat'></label>
                <select className='course-select' value={courseline} onChange={handlecourselineChange}>
                    <option value="" disabled> </option>
                    <option value={"develop"}></option>
                    <option value={"design"}></option>
                    <option value={"business"}></option>
                    <option value={"macketing"}></option>
                    <option value={"photo"}></option>
                    <option value={"music"}></option>
                </select>
                
                <div className='subcourseaddArea'>
                    <span className='subcoursename'> </span>
                    <input type='text' className='sub-cou-name' placeholder='   ' value={subcoursename} onChange={handlesubcoursenameChange}/> 
                    
                    <span className='image-url'> /URL</span>
                     <input type='text' className='image-name' placeholder='   URL  ' value={imagename} onChange={handleimagenameChange}/>
                    
                    <span className='videourl'> </span>
                    <input type='text' className='video-url' placeholder='  ' value={videourl} onChange={handlevideourlChange}/>
                </div>
                
                <span className='courseprice'> </span>
                <input type='number' className='course-price' placeholder='   ' value={courseprice} onChange={handlecoursepriceChange}/>
                
                <button className='course-reg-button' onClick={handlecourseSubmit}> </button>
            </div>
        </div>
    );
}

export function PersonalinfoPage({ onClose }) {

    const fetchProfile = async () => {
    try {
        const response = await authInstance.get(`/users/me`);
        return response.data;
    } catch (error) {
        throw error;
    }
    };

    const updateProfile = async (uid, updateData) => {
        try {
        const response = await authInstance.put(`/users/myinfo`, updateData);
        return response.data;
        } catch (error) {
        throw error;
        }
        };

    const updatePassword = async (uid, updateData) => {
        try{
            const response = await authInstance.put(`/users/password`, updateData);
            return response.data;
        } catch (error){
            throw error;
        }
    }


    const softDeleteUser = async (uid) => {
        try {
        await authInstance.delete(`/users/${uid}`);
        } catch (error) {
        throw error;
        }
    };
    
    const { user, token, signout } = useAuth();

    const [isLoading, setIsLoading] = useState(true);
    const [name, setName] = useState(user?.name || '');
    const [email, setEmail] = useState(user?.email || '');

    const [phoneNumber, setPhoneNumber] = useState('');
    const [tutorDetail, setTutorDetail] = useState('');
    
    const [currentPassword, setCurrentPassword] = useState(''); 
    const [newPassword, setNewPassword] = useState('');

    const isTutor = user && user.tutor === 'y';
    const userUid = user?.uid;

    const handlephoneNumberChange = (e) => setPhoneNumber(e.target.value);
    const handletutorDetailChange = (e) => setTutorDetail(e.target.value);
    const handleCurrentPasswordChange = (e) => setCurrentPassword(e.target.value);
    
    const handleNewPasswordChange = (e) => setNewPassword(e.target.value);

    useEffect(() => {
        if (user?.name && user?.email && !isLoading){
            setName(user.name);
            setEmail(user.email);
        }
        if (!token || !userUid){
            setIsLoading(false);
            return;
        }

        const loadProfile = async () => {
            try {
                const data = await fetchProfile(); 

                setName(data.name || user.name || '');
                setEmail(data.email || user.email || '');
                setPhoneNumber(data.phoneNumber || '');
                if (isTutor){
                    setTutorDetail(data.tutorDetail || '');
                }
            }catch (error){
                console.error("   : ", error);
                alert("    .");
                signout();
            }finally {
                setIsLoading(false);
            }
        };
        loadProfile();
    }, [token, userUid, isTutor, signout, user?.name, user?.email, isLoading]);

    const handleInfoUpdate = async () => {
        if (!userUid || !token) return alert("   .");

        const infoUpdateData = {
            phoneNumber: phoneNumber,
            ...(isTutor && { tutorDetail: tutorDetail})
        };

        try {
            await updateProfile(userUid, infoUpdateData);
            alert("  .");
        } catch (error){
            console.error("  :", error);
            alert(`  : ${error.response?.data?.message || ''}`);
        }
    };
        const handlePasswordUpdate = async () => {
        if (!userUid || !token) return alert("   .");
        if (!currentPassword || !newPassword) return alert("         .");

        const passwordUpdateData = {
            currentPassword : currentPassword,
            newPassword : newPassword,
            matchPassword : newPassword
        };
        try {
            await updatePassword(userUid, passwordUpdateData);
            alert("   .     .");
            signout();
            onClose();
        }catch (error) {
            console.error("  : ", error);
            alert(`  : ${error.response?.data?.message || ' '}`);
        }
    };

    const handleAccountDelete = async () => {
        if (!userUid || !token || typeof userUid !== 'number') {
            console.error("uid  :" , userUid);   
        } 

        if (!window.confirm("  () ?     .")){
            return;
        }

        try {
            await softDeleteUser(userUid);
            alert("  .");
            signout();
            onClose();
        } catch (error) {
            console.error("  :", error);
            alert(`  : ${error.response?.data?.message || ' '}`);
        }
    };
    
    const modalRef = useRef(null);
    const initalFocusRef = useRef(null);

    if (isLoading) {
        return(
            <div id='info-wrapper' className='loading-modal' ref={modalRef} tabIndex={-1} />
        );
    }

    return (
        <div id='info-wrapper' role='dialog' aria-modal="true" ref={modalRef} tabIndex={-1} onClick={(e) => e.stopPropagation()}>
            <div className='info-Always-area'>
                <img alt=' ' className="title-image" src="/image/ELC.svg" />
                <h2 className='info-title'>  </h2> 
            </div>
            
            <div className='infomodifyArea'>
                <span className='info-label-name'></span>
                <input type='text' className='info-readonly' value={name} readOnly disabled />
                <span className='info-label-email'></span>
                <input type='text' className='info-readonly' value={email} readOnly disabled />
                <span className='info-label-phone'></span>
                <input 
                    type='tel'
                    className='info-input-phone'
                    value={phoneNumber}
                    onChange={handlephoneNumberChange}
                    ref={initalFocusRef}
                    placeholder='010-0000-0000'
                    />
                {isTutor &&(
                    <div className='tutor-detail-area'>
                        <span className='tutor-info-label'> </span>
                        <textarea 
                            className='tutor-info-textarea'
                            value={tutorDetail}
                            onChange={handletutorDetailChange}
                            placeholder=' '
                            />
                            </div>
                )}
                <button className='update-button' onClick={handleInfoUpdate}> </button>

                <span className='info-label-currentpw'> </span>
                <input type='password' className='info-input-currentpw' value={currentPassword} onChange={handleCurrentPasswordChange}/>
                <span className='info-label-newpw'> </span>
                <input type='password' className='info-input-newpw' value={newPassword} onChange={handleNewPasswordChange} />

                <button className='password-update-button' onClick={handlePasswordUpdate}> </button>
                
                <button className='delete-account-button' onClick={handleAccountDelete} style={{ backgroundColor: '#dc3545', color: 'white' }}>
                     
                </button>
            </div>
        </div>
    );
}