import './Detail.css';
import ReviewSection from './ReviewSection';
import { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { Clock, BookOpen, Award, FileText } from 'lucide-react';
import { courseInstance } from '../axiosInstance';

function Detail() {
    const { title } = useParams(); 
    const [onTap, setOnTap] = useState('one');
    const [courseList, setCourseList] = useState([]); 
    const [mainInfo, setMainInfo] = useState(null);   
    const [loading, setLoading] = useState(true);
    const navigate = useNavigate(); 

    const difficultyMap = {
        'easy': '',
        'normal': '',
        'hard': ''
    };

    const CheckClick = () => {
        navigate(`/Checkout/${encodeURIComponent(mainInfo.lectureName)}`);
    };

    useEffect(() => {
        const fetchCourseData = async () => {
            try {
                setLoading(true);
                const response = await courseInstance.get('/popular-courses'); 
                const data = response.data;
                const decodedTitle = decodeURIComponent(title || "").trim();

                const filtered = data.filter(item => 
                    (item.lectureName || "").toString().trim() === decodedTitle
                );

                setCourseList(filtered);
                if (filtered.length > 0) setMainInfo(filtered[0]);
                setLoading(false);
            } catch (error) {
                console.error("   :", error);
                setLoading(false);
            }
        };
        if (title) fetchCourseData();
    }, [title]);

    const handleVideoPopup = (url) => {
        if(!url) return alert("   .");
        window.open(url, '_blank', 'width=1000,height=600,noopener,noreferrer');
    };

    if (loading) return <div className="loading-state">  ...</div>;
    if (!mainInfo) return <div className="no-data">    .</div>;

    return (
        <div id='detailBase'>
            <div className='detail-container'>
                <div className='detail-main-content'>
                    <div className='detailLeftup'>
                        <div className='explanarea'>
                            <div className='bestseller'></div>
                            <h1 className='courseTitle'>{mainInfo.lectureName}</h1>
                            <p className='detailex'>{mainInfo.lectureIntroduction}</p>
                            
                            <div className='info-grid'>
                                <span className='info-icon'></span>
                                <span className='info-text'>4.9 (3421 )</span>
                                <span className='info-icon'></span>
                                <span className='info-text'>23,450 </span>
                                <span className='info-icon'></span>
                                <span className='info-text'>: {mainInfo.tutorName}</span>
                            </div>
                        </div>
                        <div className='Imagearea'>
                            <img src={`/image/${mainInfo.imageName}`} alt="" />
                        </div>
                    </div>

                    <div className='detailLeftdown'>
                        <div className='detail-buttonarea'>
                            <button className={`tap-button ${onTap === "one" ? 'active' : ''}`} onClick={() => setOnTap("one")}></button>
                            <button className={`tap-button ${onTap === "two" ? 'active' : ''}`} onClick={() => setOnTap("two")}> </button>
                            <button className={`tap-button ${onTap === "three" ? 'active' : ''}`} onClick={() => setOnTap("three")}></button>
                            <button className={`tap-button ${onTap === "four" ? 'active' : ''}`} onClick={() => setOnTap("four")}> </button>
                        </div>

                        <div className='tab-content-wrapper'>
                            {onTap === "one" && (
                                <div className='curriculum-container'>
                                    <div className='curriculum-header'>
                                        <span className='section-title'>{mainInfo.lectureName}</span>
                                        <span className='section-info'>
                                             {courseList.length} 
                                        </span>
                                    </div>

                                    <ul className='curriculum-list'>
                                        {courseList.map((item) => (
                                            <li key={item.uid} className='curriculum-item'>
                                                <div className='item-left'>
                                                    <span className='play-icon'></span>
                                                    <span className='lecture-name'>{item.detailedLectureName}</span>
                                                    <button className='preview-badge' onClick={() => handleVideoPopup(item.videoAddress)}>
                                                        
                                                    </button>
                                                </div>
                                            </li>
                                        ))}
                                    </ul>
                                </div>
                            )}

                            {onTap === "two" && (
                                <div className='course-introduction'>
                                    <h2 className='intro-main-title'>{mainInfo.lectureName}</h2>
                                    <p className='intro-sub-title'> </p>
                                    <div className='intro-content-box'>
                                        <p className='intro-text'>{mainInfo.lectureIntroduction}</p>
                                    </div>
                                </div>
                            )}

                            {onTap === "three" && (
                                <div className='course-review'>
                                    <ReviewSection courseUid={mainInfo.courseUid || mainInfo.uid} />
                                </div>
                            )}
                                                                                    
                            {onTap === "four" && <div className='tutor-info'> : {mainInfo.tutorName}</div>}
                        </div>
                    </div>
                </div>
                
                <div className='detail-sidebar'>
                    <div className="sticky-container">
                        
                        <div className="sticky-label"></div>
                        <div className="sticky-price">
                            {mainInfo.price ? mainInfo.price.toLocaleString() : '0'}
                        </div>
                        
                        <button className="sticky-apply-btn" onClick={CheckClick}> </button>
                        <div className="sticky-divider" />

                        <div className="sticky-info-row">
                            <div className="sticky-icon-text"><BookOpen size={18}/> <span> </span></div>
                            <span className="sticky-info-value">{courseList.length}</span>
                        </div>
                        
                        <div className="sticky-info-row">
                            <div className="sticky-icon-text"><Award size={18}/> 
                                <span></span>
                            </div>
                            <span className="sticky-badge">
                                {difficultyMap[mainInfo.difficulty] || ''}
                            </span>
                        </div>
                        
                        <div className="sticky-info-row">
                            <div className="sticky-icon-text"><FileText size={18}/> 
                                <span> </span>
                            </div>
                            <span className="sticky-info-value">
                                {mainInfo.updateTime ? mainInfo.updateTime.split('T')[0] : ''}
                            </span>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    );
}

export default Detail;