import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import './Search.css';

const SearchPage = () => {
    const location = useLocation();
    const navigate = useNavigate();
    
    // Header  state  'searchResults'  (  )
    const searchResults = location.state?.searchResults || [];
    const query = new URLSearchParams(location.search).get('q');

    //    
    const handleRowClick = (uid) => {
        // uid        
        navigate(`/Detail?uid=${uid}`); 
    };

    return (
        <div className="search-container">
            <header className="search-header">
                <h1>"<span>{query}</span>"  </h1>
                <p> {searchResults.length}   .</p>
            </header>

            {searchResults.length > 0 ? (
                <div className="table-wrapper">
                    <table className="search-table">
                        <thead>
                            <tr>
                                <th className="col-name"></th>
                                <th className="col-intro"> </th>
                                <th className="col-tutor"></th>
                                <th className="col-price"></th>
                            </tr>
                        </thead>
                        <tbody>
                            {searchResults.map((course) => (
                                <tr key={course.uid} onClick={() => handleRowClick(course.uid)} className="selectable-row">
                                    <td className="col-name">{course.lecture_name}</td>
                                    <td className="col-intro">{course.lecture_introduction || "  ."}</td>
                                    <td className="col-tutor">{course.tutor_name || ''}</td>
                                    <td className="col-price">
                                        {course.price ? `${course.price.toLocaleString()}` : "  "}
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            ) : (
                <div className="no-results">
                    <p>  . </p>
                </div>
            )}
        </div>
    );
};

export default SearchPage;